import {registerPlugin} from '@wordpress/plugins';
import {PluginSidebar, PluginSidebarMoreMenuItem} from '@wordpress/edit-post';
import {__} from '@wordpress/i18n';
import {PanelBody, Icon} from '@wordpress/components';
import SEOIndexToggle from './components/SEOIndexToggle';
import SEOFollowToggle from './components/SEOFollowToggle';
import SEOImageIndexToggle from './components/SEOImageIndexToggle';
import SEOArchiveToggle from './components/SEOArchiveToggle';
import SEOSnippetToggle from './components/SEOSnippetToggle';
import SEODescription from './components/SEODescription';

registerPlugin('seo-gutenberg-sidebar', {
    icon: <Icon
        icon={
            <svg width='20' height='20' viewBox='0 0 576 512'>
                <path
                    d='M288 144a110.94 110.94 0 0 0-31.24 5 55.4 55.4 0 0 1 7.24 27 56 56 0 0 1-56 56 55.4 55.4 0 0 1-27-7.24A111.71 111.71 0 1 0 288 144zm284.52 97.4C518.29 135.59 410.93 64 288 64S57.68 135.64 3.48 241.41a32.35 32.35 0 0 0 0 29.19C57.71 376.41 165.07 448 288 448s230.32-71.64 284.52-177.41a32.35 32.35 0 0 0 0-29.19zM288 400c-98.65 0-189.09-55-237.93-144C98.91 167 189.34 112 288 112s189.09 55 237.93 144C477.1 345 386.66 400 288 400z'></path>
            </svg>
        }
    />,
    render: () => {
        return (
            <>
                <PluginSidebarMoreMenuItem target='seo-gutenberg-sidebar'>
                    {__('SEO Options', 'textdomain')}
                </PluginSidebarMoreMenuItem>
                <PluginSidebar name='seo-gutenberg-sidebar' title={__('SEO Options', 'textdomain')}>
                    <PanelBody intialOpen={true}>
                        <SEOIndexToggle/>
                        <SEOFollowToggle/>
                        <SEOImageIndexToggle/>
                        <SEOArchiveToggle/>
                        <SEOSnippetToggle/>
                        <SEODescription/>
                    </PanelBody>
                </PluginSidebar>
            </>
        )
    }
})